<template>
  <div>
    <header style="font-size: 200%">  </header>
    <v-card>
      <v-container fluid>
        <v-toolbar class="mb-2" color="indigo darken-5" dark flat>
          <v-toolbar-title></v-toolbar-title>
        </v-toolbar>
          <v-divider />

        <template v-if="this.$store.state.mno">
          <v-container>
            <v-row>
              <v-col>
                <v-text-field
                  v-model="user.Id"
                  label="id"
                  :disabled="true"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-divider />
            <v-row>
              <v-col>
                <v-text-field
                  v-model="user.Name"
                  label=""
                  :disabled="true"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-divider />
            <v-row>
              <v-col>
                <v-text-field
                  v-model="gender"
                  label=""
                  :disabled="edit !== true"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-divider />
            <v-row>
              <v-col>
                <v-text-field
                  v-model="user.Age"
                  label=""
                  :disabled="edit !== true"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-divider />
            <v-row>
              <v-col>
                <v-text-field
                  v-model="user.Phone"
                  label=""
                  :disabled="edit !== true"
                ></v-text-field> </v-col
            ></v-row>
            <v-divider />
            <v-row>
              <v-col>
                <v-text-field
                  v-model="user.Email"
                  label=""
                  :disabled="edit !== true"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-divider />
            <v-row>
              <v-col>
                <v-text-field
                  v-model="user.Address"
                  label=""
                  :disabled="edit !== true"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-divider />
          </v-container>
        </template>
        
        <v-toolbar class="mt-2" color="indigo" dark flat>
          <v-toolbar-title class="subheading">
            <v-btn color="blue" right @click="onClickEdit" v-if="edit === false">
              
            </v-btn>
            <template v-else>
              <v-btn color="blue" right @click="onClickCancel">
                
              </v-btn>
              <v-btn right @click="onClickSave">
                
              </v-btn>
            </template>
          </v-toolbar-title>
        </v-toolbar>
      </v-container>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {},
      gender: '',
      edit: false
    };
  },

  methods: {
    onClickEdit() {
      this.edit = true;
    },

    onClickCancel() {
      this.$http.get(`/main/personal/${this.$store.state.mno}`)
      .then(res => {
        this.user = res.data;
        if (this.user.Sex == 'F')  this.gender = '';
        else  this.gender = '';
      })
      .catch(err => {
        alert(err);
      });
      this.edit = false;
    },

    onClickSave() {
      if (confirm(" ?")) {
        if (this.gender == '')  this.user.Sex == 'F';
        else  this.user.Sex = 'M';
        this.$http.patch(`/main/personal/${this.$store.state.mno}`, this.user)
        .then(res => {
          alert(res.data.message);
        })
        .catch(err =>{
          alert(err);
        });
        this.onClickCancel();
      }
    },
  },

  created() {
    this.$http.get(`/main/personal/${this.$store.state.mno}`)
    .then(res => {
      this.user = res.data;
      if (this.user.Sex == 'F')  this.gender = '';
      else  this.gender = '';
    })
    .catch(err => {
      alert(err);
    });
  },
};
</script>
